body {
    margin: 0;
    padding: 0;
    text-align: center;
    color: #2A2A2A;
    background-color: #fff;
    font-family: BlinkMacSystemFont, Segoe WPC, Segoe UI, HelveticaNeue-Light, Ubuntu, Droid Sans, sans-serif, 'Helvetica Neue', Arial, Helvetica, sans-serif;
}

h1, h2, h3, h4, h5 {
    font-weight: 300;
}

h1 b, h2 b, h3 b, h4 b, h5 b {
    font-weight: 400;
}

b {
    font-weight: 500;
}

a {
    color: #f47421;
}

a:hover {
    text-decoration: none;
    ;
}

.mb-0 {
    margin-bottom: 0 !important;
}

.mt-4 {
    margin-top: 40px !important;
}

.my-4 {
    margin: 40px auto 40px auto;
}

.home-image {
    vertical-align: middle;
    margin-bottom: 50px;
}

.home-image .logo {
    width: 150px;
    vertical-align: middle;
}

.home-image .logo-plus {
    display: inline-block;
    font-size: 50px;
    margin: 0 15px 0 30px;
    vertical-align: middle;
}

img.footer-image {
    width: 50px;
    margin: 30px auto;
    opacity: .8;
}

img.logo-image {
    width: 200px;
    margin-top: 100px;
}

code {
    background: #272822;
    padding: 4px;
    font-size: 90%;
    color: #fd971f;
}

.code {
    background: #272822;
    display: block;
    word-break: break-all;
    padding: 20px;
    text-align: left;
    border-radius: 4px;
}

.code .id-token-0 {
    color: #cc6633;
}

.code .id-token-1 {
    color: #f9f8f5;
}

.code .id-token-2 {
    color: #fd971f;
}

.row {
    width: 100%;
    display: flex;
    text-align: left;
}

.row>.column {
    flex: 40%;
    margin-right: 15px;
}

.row>.column:last-child {
    flex: 60%;
    margin-right: 0;
}

.error-page_h6 {
    font-size: 30px;
    text-transform: uppercase;
    font-weight: 600;
    margin-top: 3rem;
    margin-bottom: 8px;
    color: #151515;
}

.error-page_p {
    font-size: 20px;
    font-weight: 400;
    margin-top: 3rem;
    margin-bottom: 3rem;
    color: #151515;
}

@media screen and (max-width: 600px) {
    .column {
        width: 100%;
        margin-right: 0;
    }
}

.container {
    background: #fff;
    border: 1px solid #e2e2e2;
    width: 980px;
    margin: 0 auto;
    margin-top: 50px;
    border-radius: 10px;
}

.json {
    text-align: left;
    display: block;
    overflow: auto;
    word-break: break-all;
}

.json .pretty-json-container.object-container {
    padding: 20px;
}

.header-title {
    background-color: #f47421;
    color: #ffffff;
    padding: 20px;
    border-radius: 10px 10px 0 0;
}

.content {
    padding: 50px;
}

.content .spa-app-description {
    line-height: 1.7;
}

.btn.primary, .btn.primary:active, .btn.primary:focus {
    background-color: #282c34;
    border-radius: 3px;
    border: none;
    color: #ffffff;
    display: inline-block;
    font-size: 1.14285714rem;
    text-align: center;
    text-decoration: none;
    width: 150px;
    cursor: pointer;
    -webkit-text-size-adjust: none;
    padding: .78571429em 1.5em .78571429em;
    margin-top: 30px;
    outline: none;
}

.sub-title {
    margin-top: 0;
}

.inline-code-block {
    border-radius: 6px;
    -moz-border-radius: 6px;
    -webkit-border-radius: 6px;
    padding: 3px 5px;
    border: 1px solid #cdcdcd;
    background-color: white;
    font-family: Monaco, Consolas, "Andale  Mono", "DejaVu Sans Mono", monospace;
}

.inline-code-block a {
    text-decoration: none !important;
}

.pointer {
    cursor: pointer;
}

.ui.attached.negative.message,
.ui.negative.message {
    background-color: #fff6f6;
    color: #9f3a38;
    box-shadow: 0 0 0 1px #e0b4b4 inset, 0 0 0 0 transparent;
    padding: 1em 1.5em;
}

.asg-json-viewer {
    background-color: #272822 !important;
    padding: 20px;
}
